.grid-container {
    display: grid;
    grid-template-columns: 1fr; /* Default: One column */
    grid-template-rows: auto auto; /* Two rows with auto height */

    
  
    /* Media query for mobile and tablet sizes */
    @media screen and (min-width: 0px) and (max-width: 767px) {
      grid-template-columns: 1fr; /* One column for mobile and tablet sizes */
      /* gap: 10px; Gap between grid items */
     
    }


  
    /* Media query for larger screens */
    @media screen and (min-width: 768px) {
      grid-template-columns: 1fr 1fr; /* Two columns for larger screens */
      /* gap: 1px; Gap between grid items */
    }
  }


  h1, h2, h3, h4, h5, h6 {

          
    /* Media query for mobile and tablet sizes */
    @media screen and (min-width: 0px) and (max-width: 767px) {
        grid-template-columns: 1fr; /* One column for mobile and tablet sizes */
        /* gap: 10px; Gap between grid items */
        margin: 0px;
        padding: 20px;
       
      }

        
    }



  